<template>
  <view class="container">
    <view class="header">{{ type }}任务列表</view>
    <view v-for="task in tasks" :key="task.id" class="card" @click="goToDetail(task)">
      <text class="title">{{ task.title }}</text>
      <text class="desc">{{ task.desc }}</text>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      type: '',
      tasks: []
    }
  },
  onLoad(options) {
    this.type = options.type || '未知类型'
    this.loadTasks()
  },
  methods: {
    loadTasks() {
      // 模拟数据，可按类型动态加载
      this.tasks = [
        { id: 1, title: '任务1'},
        { id: 2, title: '任务2'},
        { id: 3, title: '任务3' },	
		{ id: 4, title: '任务4' },
		{ id: 5, title: '任务5'},
		{ id: 6, title: '任6'},
        { id: 7, title: '任务7'},
        { id: 8, title: '任务8'},
      ]
    },
    goToDetail(task) {
      uni.navigateTo({
        url: `/pages/task-detail/task-detail?id=${task.id}&type=${this.type}`
      });
    }
  }
}
</script>

