  background: #fff;
  border-radius: 16rpx;
  padding: 30rpx;
  margin-bottom: 20rpx;
  box-shadow: 0 4rpx 8rpx rgba(0, 0, 0, 0.03);
  text-align: center;
}
.title {
  font-size: 30rpx;
  font-weight: 500;
}
.desc {
  font-size: 26rpx;
